.auth-container{
  margin: 0;
  padding: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  background-color: rgb(255, 219, 142);
}

.auth-card{
  position: relative;
  width: 350px;
  height: 300px;
  border-radius: 7px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: #fff;
  box-shadow: 2px 2px 15px rgba($color: black, $alpha: 0.2);
  p{
    margin: 5px 0;
    padding: 0;
    font-size: 30px;
    color: orange;
    font-weight: bold;
    text-decoration: underline;
  }
  input{
    margin: 5px 0;
    padding: 5px;
    width: 80%;
    height: 30px;
    border-radius: 10px;
    color: #ff9100;
    border: 2px solid orange;
    transition: .2s;
    &:focus{
      outline: none;
      border: 2px solid #ff9900;
      border-radius: 0px;
      }
  }
  button{
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100px;
    height: 30px;
    margin: 5px 0;
    background-color: orange;
    border: 2px solid orange;
    border-radius: 5px;
    color: white;
    transition: 0.3s;
    &:hover{
      background-color: white;
      color: orange;
    }
    &:disabled{
      background-color: #fff0d3;
      border: 2px solid #ffd17a;
      color: black;
    }
  }
  span{
    position: absolute;
    bottom: 15px;
    margin-top: 20px;
    color: #ff8800;
    a{
      padding: 4px 6px;
      color: white;
      border-radius: 4px;
      text-decoration: none;
      background-color: orange;
      transition: .2s;
      &:hover{
        background-color: #ffb937;
        color: white;
      }
    }
  }
}

  .loading{
    display: flex;
    justify-content: center;
    align-items: center;
    width: 20px;
    padding: 1px 5px;
    border-radius: 5px;
    background-color: #fff;
    &:after{
      content: '';
      width: 5px;
      height: 5px;
      border: 2px solid orange;
      border-radius: 50%;
      background-color: white;
      animation: 1s loading infinite ease-in;
    }
  }
  
  @keyframes loading {
    0%{
      transform: translateX(0);
    }
    25%{
      transform: translateX(-10px);
    }
    75%{
      transform: translateX(10px);
    }
    100%{
      transform: translateX(0);
    }
    
}
