body{
  font-family: Arial, Helvetica, sans-serif;
  margin: 0;
  padding: 0;
  background-color: #fff1cc;
}
h1,h2,h3,h4,h5,p,body{
  font-family: Arial, Helvetica, sans-serif;
  margin: 0;
  padding: 0;
  
}

.bg-display{
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba($color: #000000, $alpha: 0.5);
  z-index: 3;
}

body .dash-container .dash-header .display-none{
  display: none;
}
.display-none{
  display: none;
}

.dash-container{
  padding: 10px;
  width: 95%;
  margin: 10px auto;
  border-radius: 3px;
  box-shadow: 2px 2px 10px rgba($color: #000000, $alpha: 0.1);
  background-color: #fff;
  display: flex;
  flex-direction: column;
  .dash-header{
    display: flex;
    height: 80px;
    justify-content: space-between;
    h3{
      margin-left: 30px;
      margin-bottom: 13px;
      font-size: 30px;
      display: flex;
      color: orange;
      justify-content: center;
      align-items: center;
    }
    .add-note{
      align-self: center;
      margin-bottom: 13px;
      margin-right: 30px;
      width: 60px;
      height: 60px;
      background-color: orange;
      color: white;
      border: 2px solid orange;
      box-shadow: 2px 2px 5px rgba($color: #000000, $alpha: 0.2);
      border-radius: 50%;
      transition: .2s;
      &:hover{
        background-color: white;
        color: orange;
      }
      &:focus{
        outline: none;
      }
    }
    .dash-form{
      position: fixed;
      overflow: hidden;
      left: 50%;
      top: 50%;
      height: 70%;
      width: 40%;
      border-radius: 5px;
      box-shadow: 2px 2px 10px rgba($color: #000000, $alpha: 0.2);
      transform: translate(-50%, -50%);
      background-color: #fff;
      display: flex;
      flex-direction: column;
      z-index: 1000;
      .close-form{
        position: absolute;
        top: -3.5px;
        right: -2px;
        width: 35px;
        height: 35px;
        border-radius: 5px;
        text-align: center;
        line-height: 35px;
        box-shadow: inset 2px 2px 5px rgba($color: #fff, $alpha: 0.3),
                    inset -2px -2px 5px rgba($color: #fff, $alpha: 0.3);
        cursor: pointer;
        font-size: 18px;
        color: white;
        background-color: crimson;
        transition: .2s;
        &:hover{
          background-color: #f3355b;
        }
      }
      .dash-form-title{
        margin-left: -15px;
        background-color: #fff5e0;
        box-shadow: inset 2px 2px 5px rgba($color: rgb(199, 96, 0), $alpha: 0.1),
                    inset -2px -2px 5px rgba($color: rgb(221, 114, 0), $alpha: 0.1);
        margin-top: 30px;
        h3{
          color: #ffb223;
          margin: 0;
          padding: 10px;
        }
      }
      .dash-form-input{
        margin-top: 30px;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        
        input.title{
          padding: 3px 5px;
          width: 80%;
          border: 1px solid coral;
          border-radius: 3px;
          background-color: #fffaec;
        }
        textarea.content{
          width: 80%;
          height: 200px;
          resize: vertical;
          margin-top: 5px;
          padding: 3px 5px;
          border: 1px solid coral;
          border-radius: 3px;
          background-color: #fffaec;
        }
        .isupdate{
          display: flex;
          justify-content: flex-end;
          align-items: flex-end;
        }
        .btn-save{
          margin: 5px 0;
          padding: 3px 5px;
          border: 1px solid coral;
          background-color: coral;
          color: white;
          border-radius: 3px;
          transition: .3s;
          &:hover{
            background-color: #fff;
            color: coral;
          }
        }
        .btn-cancel{
          margin: 5px;
          padding: 3px 5px;
          border: 1px solid coral;
          background-color: lightcoral;
          color: white;
          border-radius: 3px;
          transition: .3s;
          &:hover{
            background-color: #fff;
            color: lightcoral;
          }
        }
      }
    }
  }
  .dash-notes{
    margin-left: 5px;
    flex: 2;
    display: flex;
    max-height: 79.5vh;
    overflow: auto;
    flex-direction: column;
    padding: 10px 10px;
    background-color: #fffaec;
    border-radius: 3px;
    box-shadow: inset 2px 2px 5px rgba($color: #000000, $alpha: 0.2);
    box-sizing: border-box;
    .card-container{
      position: relative;
      .delete-confirm{
        position: absolute;
        right: 0;
        top: 50%;
        margin-right: 10px;
        transform: translateY(-50%);
        display: flex;
        flex-direction: column;
        z-index: 1;
        transition: .3s;
        button{
          border: 2px solid red;
          border-radius: 3px;
          background-color: white;
          margin: 2px 0;
          color: red;
          transition: .2s;
          &:hover{
            transform: translateX(2px);
            box-shadow: 2px 0px 5px rgba($color: #000000, $alpha: .2);
          }
        }
        .delete-confirm-no{
          border: 2px solid orange;
          color: orange;
        }
      }
      .dash-card{
        position: relative;
        margin: 5px 0;
        background-color: coral;
        padding: 8px;
        display: flex;
        flex-direction: column;
        border-radius: 3px;
        color: white;
        box-shadow: 1px 1px 5px rgba($color: #000000, $alpha: 0.2);
        box-sizing: border-box;
        transition: .3s;
        z-index: 2;
        &.dash-card.delete-slide{
          transform: translate(-80px, -2px);
          opacity: 0.8; 
        }
        &:hover{
          background-color: #ff8e64;
          transform: translate(0, -2px);
          box-shadow: 1px 3px 5px rgba($color: #000000, $alpha: 0.2);
          cursor: pointer;
        }
        .dash-title{
          display: flex;
          h3{
            margin-right: 10px;
          }
          p{
            background-color: #fff;
            padding: 1px 5px;
            margin: 3px 0;
            border-radius: 3px;
            color: coral;
          }
        }
        .btn-delete{
          background-color: maroon;
          width:60px;
          border-radius: 3px;
          text-align: center;
          align-self: flex-end;
          padding: 2px;
          &:hover{
            background-color: #ae0606;
          }
        }
      }
    } 
  }
}

@media (max-width: 768px){
  .dash-container .dash-header .dash-form{
    height: 75%;
    width: 80%;
  }
}
